.library-empty{
    width: 100%;
    text-align: center;
}

.library-empty__title{
    margin-bottom: 15px;
    font-size: 28px;
    font-weight: 700;
    color: $accent-color;

    @media screen and (min-width: 769px) {
        font-size: 36px;
        margin-bottom: 20px;
    }
}

.library-empty__text{
    font-size: 16px;

    &:not(:last-child){
        font-weight: 500;
        margin-bottom: 10px;
    };

    @media screen and (min-width: 769px) {
        font-size: 18px;
    }
}

.remove-film-btn {
    position: absolute;
    top: 5px;
    right: 5px;
    display: flex;

    align-items: center;

    // min-width: 35px;
    // min-height: 35px;
    // padding: 5;
    // margin-left: auto;

    fill: $main-text-color;
    // background-color: $main-bg-color;
    // border: 1px solid $third-text-color;
    border-radius: 5px;

    &:hover,
    &:focus {
        color: $main-text-color;
        background-color: $main-text-color;
        border: 1px solid $accent-color;
    }
}

.remove-film-svg {
    .remove-film-btn:focus &,
    .remove-film-btn:hover & {
        fill: $accent-color;
    }
}
