.footer {
  &--wrap {
    padding-top: 30px;
    padding-bottom: 30px;
    text-align: center;
  }

  &__text {
    font-weight: 400;
    font-size: 14px;
    line-height: 16px;
    color: $second-text-color;
    @media screen and (min-width: 768px) {
      font-size: 16px;
      line-height: 19px;
    }
  }

  &__text span {
    @media screen and (max-width: 767px) {
      display: block;
      margin-bottom: 10px;
    }
  }

  &__btn {
    text-decoration: underline;
    font-weight: 400;
    font-size: 14px;
    line-height: 16px;
    color: $second-text-color;
    transition: color 200ms cubic-bezier(0.4, 0, 0.2, 1);

    &:hover,
    &:focus {
      color: $accent-color;
    }

    @media screen and (min-width: 768px) {
      font-size: 16px;
      line-height: 19px;
    }
  }

  &__icon {
    margin-right: 10px;
    margin-left: 10px;
  }
}
